.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#name1{
  line-height:0.1vh;
  font-size: 5vh;
}

#name{
  line-height: 0.1vh;
}

#table {
  display: flex;  
  
  align-items: center;
  flex-direction: column;
  background-color:skyblue;
  width:35vw;
  height:70vh;
  padding-top:2vh;
  border-style: ridge;
  border-width: 1vh;
