}

#body {
  display: flex;
  background-color:wheat;
  justify-content: center;
  align-items: center;
  padding-top:3vh;
  
}



.text1 {
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 40px;
  text-align: center;
  padding-top:2vh;
  text-shadow: 1px 1px 1px #464646,
  1px 2px 1px #464646,
  1px 3px 1px #464646,
  1px 4px 1px #464646,
  1px 5px 1px #525252,
  1px 6px 1px #525252,
  1px 7px 1px #525252,
  1px 8px 1px #1bff2e,
